import { useEffect, useState } from "react";
import axios from "axios";
import NavBar from './NavBar';

function ChatTest() {
  const [userId, setUserId] = useState(null);
  const [question, setQuestion] = useState("");
  const [answer, setAnswer] = useState("");
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    axios.get("http://localhost:8080/users/user-id", { withCredentials: true })
      .then(res => setUserId(res.data))
      .catch(err => {
        console.error("    ", err);
      });
  }, []);

  const handleSend = async () => {
    if (!question.trim()) return;
    if (!userId) {
      setAnswer("     .");
      return;
    }

    setLoading(true);
    setAnswer(""); //     
    try {
      const res = await axios.post("http://localhost:8000/chat/chat", {
        user_id: userId,
        question,
      });
      setAnswer(res.data.answer);
    } catch (err) {
      setAnswer(`  : ${err.response?.data?.detail || err.message}`);
    } finally {
      setLoading(false);
    }
  };

  return (
    <>
                <NavBar />
      <div style={styles.container}>
        <h1 style={styles.header}> Gemini </h1>

        {!userId ? (
          <p style={styles.status}>     ...</p>
        ) : (
          <>
            <div style={styles.chatBox}>
              {/*    */}
              {question && (
                <div style={{ ...styles.bubble, ...styles.userBubble }}>
                  {question}
                </div>
              )}

              {/*    */}
              {loading ? (
                <div style={{ ...styles.bubble, ...styles.botBubble }}>
                  <span className="blinking">  ...</span>
                </div>
              ) : (
                answer && (
                  <div style={{ ...styles.bubble, ...styles.botBubble }}>
                    <pre style={styles.pre}>{answer}</pre>
                  </div>
                )
              )}
            </div>

            <div style={styles.inputArea}>
              <textarea
                value={question}
                onChange={(e) => setQuestion(e.target.value)}
                placeholder=" "
                rows="4"
                style={styles.textarea}
              />
              <button onClick={handleSend} disabled={loading} style={styles.button}>
                {loading ? " ..." : " "}
              </button>
            </div>
          </>
        )}
      </div>
      </>
  );
}

const styles = {
  container: {
    maxWidth: "800px",
    margin: "0 auto",
    padding: "2rem",
    fontFamily: "Segoe UI, sans-serif",
    background: "#f9f9f9",
    minHeight: "100vh",
  },
  header: {
    fontSize: "2rem",
    marginBottom: "1rem",
    textAlign: "center",
    color: "#333",
  },
  status: {
    fontStyle: "italic",
    color: "#666",
  },
  chatBox: {
    display: "flex",
    flexDirection: "column",
    gap: "1rem",
    marginBottom: "1.5rem",
    padding: "1rem",
    backgroundColor: "#fff",
    borderRadius: "12px",
    boxShadow: "0 2px 8px rgba(0,0,0,0.05)",
    minHeight: "200px",
  },
  bubble: {
    padding: "0.75rem 1rem",
    borderRadius: "16px",
    maxWidth: "100%", //     
    wordBreak: "break-word", //    
    overflowWrap: "break-word", //  
    whiteSpace: "pre-wrap", //    +  
    lineHeight: "1.6",
  },
  userBubble: {
    backgroundColor: "#DCF8C6",
    alignSelf: "flex-end",
  },
  botBubble: {
    backgroundColor: "#ECECEC",
    alignSelf: "flex-start",
  },
  inputArea: {
    display: "flex",
    flexDirection: "column",
    gap: "0.5rem",
  },
  textarea: {
    width: "100%",
    padding: "1rem",
    fontSize: "1rem",
    borderRadius: "8px",
    border: "1px solid #ccc",
    resize: "none",
  },
  button: {
    alignSelf: "flex-end",
    backgroundColor: "#4F46E5",
    color: "#fff",
    padding: "0.75rem 1.25rem",
    border: "none",
    borderRadius: "8px",
    cursor: "pointer",
    fontSize: "1rem",
  },
  pre: {
    margin: 0,
    fontFamily: "inherit",
    wordBreak: "break-word",
    overflowWrap: "break-word",
    whiteSpace: "pre-wrap",
  },
};

export default ChatTest;
