import React, { useEffect } from 'react';
import './css/kakaomap.css';

export default function KakaoMap({ stores }) {
  console.log("  stores:", stores);
  stores.forEach(store => console.log("  :", store.real_address));

  useEffect(() => {
    const loadKakaoMapScript = () => {
      if (window.kakao && window.kakao.maps) {
        console.log("  kakao  ");
        window.kakao.maps.load(initMap);
        return;
      }

      const script = document.createElement("script");
      script.src =
        "//dapi.kakao.com/v2/maps/sdk.js?appkey=cdcb36f7c5dbf63f04c19c92959118ca&autoload=false&libraries=services";
      script.async = true;

      script.onload = () => {
        console.log(" Kakao SDK   ");
        window.kakao.maps.load(() => {
          console.log(" kakao.maps.load ", window.kakao);
          initMap();
        });
      };

      script.onerror = () => {
        console.error(" Kakao SDK   -     ");
      };

      document.head.appendChild(script);
    };

    const initMap = () => {
      if (!stores || stores.length === 0) return;

      const filteredStores = stores.filter(
        (store) => store.real_address && store.real_address.trim().length > 5
      );

      console.log("  stores (  ):", filteredStores);
      if (filteredStores.length === 0) return;

      const container = document.getElementById("map");
      const options = {
        center: new window.kakao.maps.LatLng(37.5665, 126.9780), //   ( )
        level: 5,
      };
      const map = new window.kakao.maps.Map(container, options);
      const geocoder = new window.kakao.maps.services.Geocoder();

      const bounds = new window.kakao.maps.LatLngBounds(); //    

      let markerCount = 0;

      filteredStores.forEach((store) => {
        geocoder.addressSearch(store.real_address, function (result, status) {
          console.log(
            `    [${store.name}]`,
            store.real_address,
            status,
            result
          );

          if (status === window.kakao.maps.services.Status.OK) {
            const coords = new window.kakao.maps.LatLng(result[0].y, result[0].x);
            bounds.extend(coords); //    

            const marker = new window.kakao.maps.Marker({
              map: map,
              position: coords,
            });

            const info = new window.kakao.maps.InfoWindow({
                content: `
                  <div style="
                    padding:10px;
                    font-size:14px;
                    line-height:1.6;
                    max-width:200px;
                    word-break:break-word;
                  ">
                    <div style="font-weight:bold; font-size:15px;"> ${store.name}</div>
                    <div style="margin-top:4px; color:#444;">
                       ${store.rating || "0.0"}  ${store.review || " "}
                    </div>
                    <a href="${store.url}" target="_blank"
                       style="display:inline-block; margin-top:6px; color:#2b6cb0; font-weight:bold; text-decoration:none;"
                       onmouseover="this.style.color='#1a4e8a'"
                       onmouseout="this.style.color='#2b6cb0'">
                        
                    </a>
                  </div>
                `,
              });

            marker.setMap(map);
            window.kakao.maps.event.addListener(marker, "click", () => {
              info.open(map, marker);
            });

            markerCount++;
            //        
            if (markerCount === filteredStores.length) {
              map.setBounds(bounds);
            }
          }
        });
      });
    };

    loadKakaoMapScript();
  }, [stores]);

  return (
    <div
      id="map"
      style={{ width: "100%", height: "400px", marginTop: "20px" }}
    />
  );
}
